/* Custom Loader CSS */

.custom-loader {
    margin: 260px auto !important;
}

.v-loading-indicator[style*="none"] {
    display: block!important;
    width: 100%!important;
    opacity: 0;
    -webkit-animation: none;
    -moz-animation: none;
    animation: none;
    -webkit-transition: opacity 500ms 300ms, width 300ms;
    -moz-transition: opacity 500ms 300ms, width 300ms;
    transition: opacity 500ms 300ms, width 300ms;
}

.v-loading-indicator-delay {
    width: 90%;
    -webkit-animation: v-progress-delay 3.8s forwards;
    -moz-animation: v-progress-delay 3.8s forwards;
    animation: v-progress-delay 3.8s forwards;
}

.v-loading-indicator {
    position: fixed!important;
    z-index: 99999;
    left: 0;
    right: auto;
    top: 0;
    width: 100%;
    opacity: 1;
    height: 4px;
    background-color: #ff3154;
    pointer-events: none;
    -webkit-transition: none;
    -moz-transition: none;
    transition: none;
    -webkit-animation: v-progress-start 1000ms 200ms both;
    -moz-animation: v-progress-start 1000ms 200ms both;
    animation: v-progress-start 1000ms 200ms both;
}


/* Custom Loader CSS End */

