/* Dark and Light Theme Switch CSS */

.main {
    background: var(--background);
    color: var(--on-background);
    height: 100vh;
    position: relative;
    padding-bottom: 61vh;
}

.innerdiv {
    text-align: right;
    position: absolute;
    right: 55px;
    top: 27px;
    width: 20px;
    color: #FFF;
    cursor: pointer;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    top: 11px;
    margin: 0 5px;
}

.innerDiv input {
    display: none;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: #FF0070;
}

input:focus+.slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.lightBulbIcon {
    color: #464ad0;
    width: 14px;
    cursor: pointer;
}

.lightBulbIcon:focus {
    color: #000;
}

.addLightColor {
    color: #000 !important;
}


/* Dark and Light Theme Switch CSS End */

